<template>
  <div>
    <div id="myDiagramDiv"></div>
    <button @click="addele"></button>
  </div>
</template>
<script>
import go from "gojs";
export default {
  name: "GoVue",
  data() {
    return {
      eleArr: [
        { key: "Alpha", color: "lightblue" },
        { key: "Beta", color: "orange" },
        { key: "Gamma", color: "lightgreen" },
        { key: "Delta", color: "pink" },
      ],
      relationArr: [
        { from: "Alpha", to: "Beta" },
        { from: "Alpha", to: "Gamma" },
        { from: "Beta", to: "Beta" },
        { from: "Gamma", to: "Delta" },
        { from: "Delta", to: "Alpha" },
      ],
    };
  },
  methods: {
    init() {
      var $ = go.GraphObject.make; // for conciseness in defining templates
      var myDiagram = $(
        go.Diagram,
        "myDiagramDiv", // create a Diagram for the DIV HTML element
        {
          // enable undo & redo
          "undoManager.isEnabled": true,
        }
      );
      // define a simple Node template
      myDiagram.nodeTemplate = $(
        go.Node,
        "Auto", // the Shape will go around the TextBlock
        $(
          go.Shape,
          "RoundedRectangle",
          { strokeWidth: 0, fill: "white" }, // default fill is white
          // Shape.fill is bound to Node.data.color
          new go.Binding("fill", "color")
        ),
        $(
          go.TextBlock,
          { margin: 8 }, // some room around the text
          // TextBlock.text is bound to Node.data.key
          new go.Binding("text", "key")
        )
      );

      // but use the default Link template, by not setting Diagram.linkTemplate

      // create the model data that will be represented by Nodes and Links
      myDiagram.model = new go.GraphLinksModel(this.eleArr, this.relationArr);
    },
    addele() {
      this.eleArr.push({ key: "yuwenjie", color: "pink" });
      this.init();
    },
  },
  mounted() {
    this.init();
  },
};
</script>
<style >
#myDiagramDiv {
  width: 500px;
  height: 500px;
}
</style>